<template>
  <div class="scenic-world">
    <canvas ref="canvas" />
  </div>
  <RouterView />
  <div class="label">coming soon..</div>
</template>

<script>
import { ref, onMounted, onUnmounted } from 'vue'
import App from "@/graphics/index";

export default {
  setup() {
    const canvas = ref(null);
    let app = null;
    
    onMounted(() => {
      app = new App({ canvas: canvas.value });
    });
    onUnmounted(() => {
      app.destroy();
    });
    return {
      canvas,
    };
  },
}
</script>


